.p-datatable .p-sortable-column.p-highlight {
    background: #f8f9fa;
    color: #ff5d4f;
}
.p-datatable .p-sortable-column.p-highlight .p-sortable-column-icon {
    color: #ff5d4f;
}
.p-datatable .p-sortable-column.p-highlight:hover {
    background: #e9ecef;
    color: #ff5d4f;
}
.p-datatable .p-sortable-column.p-highlight:hover .p-sortable-column-icon {
    color: #ff5d4f;
}
.p-datatable .p-sortable-column:focus {
    box-shadow: inset 0 0 0 0.15rem #ff5d4f;
    outline: 0 none;
}

.p-inputtext:enabled:hover {
    border-color: #ff5d4f;
}
.p-inputtext:enabled:focus {
    outline: 0 none;
    outline-offset: 0;
    box-shadow: 0 0 0 0.2rem #fd887e;
    border-color: #ff5d4f;
}

.link {
    color: #04b0ff;
    border-bottom: 1px solid;
    font-size: 12px;
    cursor: pointer;
}

.infomracao {
    font-size: 12px;
    color: #0891cf;
}

.info-tabela {
    font-size: 1rem;
    color: #04b0ff;
}

.obrigatorio:after {
    content: " * ";
    color: red;
}

.campoInvalido:after {
    font-size: 10px;
    color: red;
    content: " Campo obrigatório! ";
}

.campoInvalido::before {
    font-family: FontAwesome;
    content: "\f071";
    font-size: 10px;
    color: red;
}

.c-primary {
    color: $colorLabel;
}

.c-secundary {
    color: $textColorBasic;
}

// Staps
.p-steps.steps-custom {
