}

body .p-steps .p-steps-item .p-menuitem-link .p-steps-number {
    color: $textColorBasic;
    border: 1px solid $colorBorder;
}

.p-steps.steps-custom .p-steps-item .p-steps-title {
    color: $titleColor;
}

body .p-steps .p-steps-item.p-state-highlight .p-steps-number {
    background: $buttonbackgroundPimary !important;
    color: white !important;
}

.p-steps .p-steps-item.p-highlight .p-steps-number {
    background: #ee4232 !important;
}

.line-title {
