.p-placeholder {
    color: $colorPlaceHolder !important;
}

// Overlay
.p-overlaypanel .p-overlaypanel-close {
    background: none;
    color: #88777d;
    top: 0;
    right: 0;
}
.p-overlaypanel .p-overlaypanel-close:enabled:hover {
    background: #dbdbdb;
    color: #88777d;
}

// Barras de rolagens
::-webkit-input-placeholder {
    /* WebKit, Blink, Edge */
    color: $colorPlaceHolder !important;
}
:-moz-placeholder {
    /* Mozilla Firefox 4 to 18 */
    color: $colorPlaceHolder !important;
    opacity: 1;
}
::-moz-placeholder {
    /* Mozilla Firefox 19+ */
    color: $colorPlaceHolder !important;
    opacity: 1 !important;
}
:-ms-input-placeholder {
    /* Internet Explorer 10-11 */
    color: $colorPlaceHolder !important;
}
::-ms-input-placeholder {
    /* Microsoft Edge */
    color: $colorPlaceHolder !important;
}

::placeholder {
    /* Most modern browsers support this now. */
    color: $colorPlaceHolder !important;
}

// tabelas
.p-table table {
    font-size: 14px;
}

.icon-sort-table {
