    display: none !important;
}
.p-column-filter-clear-button,
.p-column-filter-menu-button {
    height: 15px !important;
    span {
        font-size: 12px !important;
    }
}

.p-column-filter-menu {
    width: 15px;
    height: 15px;
}

// tabelas paginação

$btHeightPaginator: 25px;
.p-datatable .p-paginator-bottom {
    height: 40px;
    font-size: 14px;
    padding: 0px 12px 0 12px;
}
.p-datatable .p-datatable-thead > tr > th {
    font-size: 14px;
    padding: 1rem 0.5rem;
}

.p-paginator .p-paginator-pages .p-paginator-page.p-highlight {
    height: 25px;
    background: $buttonbackgroundPimary;
    border-color: $buttonTextPrimary;
}

.p-paginator .p-paginator-pages .p-paginator-page.p-highlight:hover {
    background: $buttonBackgroundPrimaryHover;
    border-color: $buttonBackgroundPrimaryHover;
}

.p-paginator .p-dropdown {
    margin-left: 0.5rem;
    height: 28px;
    padding: 0;
    margin: 0;
}

.p-paginator .p-dropdown .p-dropdown-label {
    padding: 0px 0 0 9px;
    font-size: 14;
    margin-top: 3px;
    height: auto;
}

.p-paginator .p-paginator-first {
    height: $btHeightPaginator;
}
.p-paginator .p-paginator-prev {
    height: $btHeightPaginator;
}

.p-paginator .p-paginator-next {
    height: $btHeightPaginator;
}

.p-paginator .p-paginator-last {
    height: $btHeightPaginator;
}

